import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);


export default new Vuex.Store({
  state: {
    STORAGE_KEY: 'currentUser',
    todos: []
  },
  getters: {
    doneTodos: state => {
      return state.todos;
    }
  },
  mutations: {
    listtodo (state, todos) {
      state.todos = todos;
    }
  },

  actions: {},

  modules: {}
});
