<template>
  <div class="profile">
    <div class="row justify-content-center">
      <div
        class="col-12 col-sm-12 col-md-12 col-lg-6 bg-app justify-content-start position-relative"
      >
        <div class="header-app">
          <h1 class="text-uppercase">
            <i class="fab fa-facebook-messenger"></i>
          </h1>
        </div>

        <div class="content-app content-app--group">

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import fire from "@/store/fire";
// import fire from "../store/fire";
// Register a global custom directive called `v-focus`
Vue.directive("focus", {
  inserted(el) {
    el.focus();
  }
});

export default {
  name: "Profile",
  // HelloWorld
  components: {},

  data() {
    return {
      btnRemove: false,
      interval: false,
      count: 0,
      rooms: [],
      newRoom: {
        name: "",
        listUsers: []
      },
      error: ""
    };
  },

  beforeCreate() {
    fire.auth().onAuthStateChanged(function(user) {
      if (user == null) {
        window.location.href = "/message-app/#/login";
      }
    });
  },

  mounted() {
    this.fetch();
  },

  methods: {
    logout() {
      fire
        .auth()
        .signOut()
        .then(() => {
          fire.auth().onAuthStateChanged(function(user) {
            if (user == null) {
              window.location.href = "/message-app/#/login";
            }
          });
        })
        .catch(error => {
          error.log(error);
        });
    },

    removeRoom(id) {
      fire
        .database()
        .ref("/comments")
        .orderByChild("uidRoom")
        .equalTo(id)
        .on("value", snapshot => {
          let data = snapshot.val();
          if (data != null) {
            Object.keys(data).forEach(key => {
              fire
                .database()
                .ref("/comments/" + key)
                .remove();
            });
          }
        });
      fire
        .database()
        .ref("/rooms/" + id)
        .remove();

      this.fetch();
    },

    start(index) {
      if (!this.interval) {
        this.interval = setInterval(() => {
          this.rooms.map(val => (val.isDelete = false)),
            (this.rooms[index].isDelete = true);
        }, 500);
      }
    },
    stop() {
      clearInterval(this.interval);
      this.interval = false;
    },

    addRoom() {
      fire
        .database()
        .ref("/rooms")
        .push({
          name: this.newRoom.name,
          listUsers: [this.fetchLocalStorage()]
        });
      this.newRoom.name = "";
    },

    fetchLocalStorage() {
      return JSON.parse(
        localStorage.getItem(this.$store.state.STORAGE_KEY) || null
      );
    },

    filter(list) {
      let list1 = [];
      fire.auth().onAuthStateChanged(function(user) {
        list.forEach(val1 => {
          if (
            val1.listUser.filter(val2 => val2 === user.uid)[0] !== undefined
          ) {
            list1.push(val1);
          }
        });
      });
      return list1;
    },
    fetch() {
      fire
        .database()
        .ref("/rooms")
        .on("value", snapshot => {
          let listRoom = [];
          let data = snapshot.val();
          let listUser = [];
          if (data != null) {
            Object.keys(data).forEach(key => {
              listUser = [];
              data[key].listUsers.map(val => listUser.push(val));
              listRoom.push({
                id: key,
                name: data[key].name,
                listUser: listUser,
                isDelete: false
              });
              this.rooms = this.filter(listRoom);
            });
          } else {
            this.rooms = [];
          }
        });
    }
  }
};
</script>
