<script>
import Vue from "vue";
import fire from "../store/fire";
import InfiniteLoading from "vue-infinite-loading";

Vue.directive("focus", {
  inserted(el) {
    el.focus();
  }
});

export default {
  name: "Home",
  components: {
    InfiniteLoading
  },

  data() {
    return {
      rowText: 1,
      recordings: [],
      showRecorder: true,
      headers: {
        "X-Custom-Header": "some data"
      },
      list: [],
      page: 1,
      totalItems: 0,
      totalPage: 10,
      listComments: [],
      infiniteId: +new Date(),
      newMemberText: "",
      newMember: [],
      message: {
        success: "",
        error: ""
      },
      newComment: {
        message: "",
        dateTime: "",
        currentUser: String,
        imageTest: "",
        wishlist: false
      },
      reset: false
    };
  },

  beforeCreate() {
    fire.auth().onAuthStateChanged(user => {
      if (user == null) {
        localStorage.removeItem("myPage.expectSignIn");
        window.location.href = "/message-app/#/login";
      } else {
        localStorage.setItem("myPage.expectSignIn", "1");
        this.currentUser = user.uid;
      }
    });
  },

  watch: {
    test() {
      var container = this.$el.querySelector("#container");
      container.scrollTop = container.scrollHeight;
      console.log(container.scrollTop);
    }
  },

  created() {},

  computed: {},

  mounted() {
    setTimeout(() => {
      // this.getList2Data();
    }, 2000);
    // let urlImg =
    //   "https://tpc.googlesyndication.com/simgad/17693458774618797709?sqp=4sqPyQQ7QjkqNxABHQAAtEIgASgBMAk4A0DwkwlYAWBfcAKAAQGIAQGdAQAAgD-oAQGwAYCt4gS4AV_FAS2ynT4&rs=AOga4qlmZohdyfcJ7TONC0HrANFRhTVnbw";
    // this.toDataURL(urlImg, function(val) {
    //   this.imageTest = val;
    // });
    setTimeout(() => {
      fire.auth().onAuthStateChanged(user => {
        if (!user) {
          fire
            .database()
            .ref("/users/")
            .once("value", function(snapshot) {
              snapshot.forEach(function(child) {
                child.ref.update({
                  status: 0
                });
              });
            });
        } else {
          fire
            .database()
            .ref("/users/" + user.uid)
            .update({ status: "1" });
        }
      });
    });
  },

  updated() {},

  methods: {
    addStatusWishList(value, id) {
      fire
        .database()
        .ref("/comments/" + id)
        .update({ wishlist: value });
      this.changeType();
    },

    deleteRow() {
      if (this.rowText > 1) {
        this.rowText--;
      }
    },

    selectEmoji(emoji) {
      this.newComment.message = this.newComment.message + ` ${emoji.data}`;
    },

    onResult(data) {
      const reader = new window.FileReader();
      reader.readAsDataURL(data);
      reader.onloadend = () => {
        let base64 = reader.result + "";
        base64 = base64.split(",")[1];
        this.newComment.message = base64;
        this.addRecording();
      };
    },

    addRecording() {
      let value = this.newComment.message && this.newComment.message.trim();

      if (!value || this.currentUser === null) {
        return;
      }
      // this.scrollToEnd();
      this.addCommentFB(1);
      this.changeType();
      this.newComment = {};
      this.reset = true;
    },

    covertBase64ToBlob(base64, type) {
      if (type === 1) {
        const ab = new ArrayBuffer(base64.length);
        const buff = new Buffer.from(base64, "base64");

        const view = new Uint8Array(ab);
        for (let i = 0; i < buff.length; ++i) {
          view[i] = buff[i];
        }
        const byteArray = new Uint8Array(view);
        const blob = new Blob([byteArray]);
        return window.URL.createObjectURL(blob);
      }
    },

    changeType() {
      this.totalItems = 0;
      this.totalPage = 10;
      this.page = 1;
      this.listComments = [];
      this.infiniteId += 1;
    },

    newline() {
      if (this.rowText < 5) {
        this.rowText++;
      }
      this.newComment.message = `${this.newComment.message}\n`;
    },

    addMember(newMember) {
      let listRoom = [];

      fire
        .database()
        .ref("/rooms/" + this.$route.params.id)
        .on("value", snapshot => {
          let data = snapshot.val();
          Object.keys(data.listUsers).map(key =>
            listRoom.push(data.listUsers[key])
          );
        });

      listRoom.push(newMember.id);

      fire
        .database()
        .ref("/rooms/" + this.$route.params.id + "/listUsers")
        .update(listRoom);

      this.message.success = "Add user success";
    },

    findMember() {
      this.newMember = [];
      let name = this.newMemberText.trim();
      let listusers = [];

      fire
        .database()
        .ref("/users")
        .on("value", snapshot => {
          let data = snapshot.val();
          Object.keys(data).forEach(key => {
            console.log(this.findMemberAdded(key));
            if (data[key].name === name && this.findMemberAdded(key) == true) {
              listusers.push({
                id: key,
                name: data[key].name
              });
            }
          });
        });
      this.newMember.length == 0
        ? (this.message.error = "Find not found")
        : (this.message.error = "");
      this.newMember = listusers;
    },

    findMemberAdded(list) {
      let listNewUser = true;

      fire
        .database()
        .ref("/rooms/" + this.$route.params.id + "/listUsers")
        .on("value", snapshot => {
          let data = snapshot.val();
          Object.keys(data).forEach(key => {
            if (list === data[key]) return (listNewUser = false);
          });
        });

      return listNewUser;
    },

    addComment() {
      let value = this.newComment.message && this.newComment.message.trim();

      if (!value || this.currentUser === null) {
        return;
      }

      this.addCommentFB(0);
      this.changeType();
      (this.newComment = {
        message: "",
        dateTime: "",
        currentUser: String,
        imageTest: "",
        wishlist: false
      }),
        (this.reset = true);
    },

    addCommentFB(type) {
      fire
        .database()
        .ref("/comments")
        .push({
          message: this.newComment.message,
          dateTime: Date.now(),
          uidUser: this.currentUser,
          uidRoom: this.$route.params.id,
          type: type,
          wishlist: false
        });
    },

    toDataURL(src, callback) {
      var image = new Image();
      image.crossOrigin = "Anonymous";

      image.onload = function() {
        var canvas = document.createElement("canvas");
        var context = canvas.getContext("2d");
        canvas.height = this.naturalHeight;
        canvas.width = this.naturalWidth;
        context.drawImage(this, 0, 0);
        var dataURL = canvas.toDataURL("image/jpeg");
        callback(dataURL);
      };

      image.src = src;
    },

    fetch($state) {
      let idRoom = this.$router.currentRoute.params.id;
      let newtotal = 10;
      let listComments1 = [];

      fire
        .database()
        .ref("/comments")
        .orderByChild("uidRoom")
        .equalTo(idRoom)
        .limitToLast(this.page * 10)
        .on("value", snapshot => {
          let data = snapshot.val();
          if (data != null) {
            this.page += 1;
            Object.keys(data).forEach(key => {
              listComments1.push({
                id: key,
                src: this.covertBase64ToBlob(data[key].message, data[key].type),
                message: data[key].message,
                dateTime: this.convertDate(data[key].dateTime),
                uidUser: data[key].uidUser,
                uidRoom: data[key].uidRoom,
                type: data[key].type,
                wishlist: data[key].wishlist
              });
              this.listComments = listComments1;
              newtotal = listComments1 + 1;
            });
            $state.loaded();
          } else {
            $state.complete();
          }
          this.totalItems == newtotal
            ? $state.complete()
            : (this.totalItems = newtotal);
        });
    },

    convertDate(date) {
      if (!date) {
        return "-";
      }
      function formatFunc(str) {
        return str > 9 ? str : "0" + str;
      }

      let result = "";

      let date2 = new Date(date);
      let today = new Date();

      let yearToday = today.getFullYear();
      let monToday = formatFunc(today.getMonth() + 1);
      let dayToday = formatFunc(today.getDate());
      let hourToday = formatFunc(today.getHours());
      let minToday = formatFunc(today.getMinutes());

      let year = date2.getFullYear();
      let mon = formatFunc(date2.getMonth() + 1);
      let day = formatFunc(date2.getDate());
      let hour = formatFunc(date2.getHours());
      let min = formatFunc(date2.getMinutes());
      let second = formatFunc(date2.getSeconds());

      if (yearToday == year && monToday == mon) {
        result = `${dayToday - day} ngày trước`;
        if (dayToday - day == 0) {
          result = "Today";
          if (dayToday >= day) {
            result = `${hourToday - hour} giờ trước`;
            if (hourToday - hour == 0) {
              if (minToday >= min) {
                result = `${minToday - min} phút trước`;
                if (minToday - min == 0) {
                  result = "Bây giờ";
                }
              }
            }
          }
        }
      } else {
        result =
          year + "-" + mon + "-" + day + " " + hour + ":" + min + ":" + second;
      }
      return result;
    }

    /*    fetchLocalStorage() {
      return JSON.parse(
        localStorage.getItem(this.$store.state.STORAGE_KEY) || null
      );
    }*/
  }
};
</script>
