<template>
  <div class="login">
    <div class="row justify-content-center">
      <div class="col-12 col-sm-12 col-md-12 col-lg-6 bg-app">
        <form @submit.prevent="createUser">
          <div class="form-auth">
            <h1>Register</h1>
            <div class="form-group">
              <div
                v-if="$v.form.$error"
                class="alert alert-danger mb-4"
                role="alert"
              >
                <p
                  v-if="!$v.form.register_id.email"
                  class="mb-0"
                  v-text="'Error - Email'"
                />
                <p
                  v-if="!$v.form.register_id.required"
                  class="mb-0"
                  v-text="'Error - Email'"
                />
                <p
                  v-if="!$v.form.register_password.required"
                  class="mb-0"
                  v-text="'Error - Password'"
                />

                <p
                  v-if="!$v.form.register_name.required"
                  class="mb-0"
                  v-text="'Error - Name'"
                />
              </div>

              <div class="alert alert-danger" role="alert" v-if="message.error">
                {{ message.error }}
              </div>
            </div>
            <div class="form-group">
              <div
                class="alert alert-success"
                role="alert"
                v-if="message.success"
              >
                {{ message.success }}
              </div>
            </div>
            <div class="form-group">
              <label for="register_id">Email address</label>
              <input
                type="text"
                class="form-control"
                id="register_id"
                name="register_id"
                v-model.trim="$v.form.register_id.$model"
                placeholder="Enter email"
              />
            </div>
            <div class="form-group">
              <label for="register_password">Password</label>
              <input
                type="password"
                class="form-control"
                id="register_password"
                name="register_password"
                v-model.trim="$v.form.register_password.$model"
                placeholder="Password"
              />
            </div>
            <div class="form-group mb-4">
              <label for="register_name">Name</label>
              <input
                type="text"
                class="form-control"
                id="register_name"
                name="register_name"
                v-model.trim="$v.form.register_name.$model"
                placeholder="Name"
              />
            </div>
            <div class="row">
              <div class="col-6">
                <a
                  v-bind:href="'/message-app/#/login'"
                  class="btn btn-outline-light w-100"
                  v-text="'Back'"
                />
              </div>
              <div class="col-6">
                <button
                  type="submit"
                  class="btn btn-custom"
                  v-text="'Register'"
                  :disabled="$v.form.$invalid || isSubmit"
                />
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import fire from "../store/fire";
import { required, email } from "vuelidate/lib/validators";

Vue.directive("focus", {
  inserted(el) {
    el.focus();
  }
});

export default {
  name: "Home",

  data() {
    return {
      isSubmit: false,
      form: {
        register_id: "",
        register_password: "",
        register_name: ""
      },
      message: {
        success: "",
        error: ""
      }
    };
  },

  validations: {
    form: {
      register_id: { required, email },
      register_password: { required },
      register_name: { required }
    }
  },

  methods: {
    async createUser() {

      this.isSubmit = true;
      if (this.$v.form.$invalid) {
        this.isSubmit = false;
        return false;
      }

      await fire
        .auth()
        .createUserWithEmailAndPassword(
          this.$v.form.register_id.$model,
          this.$v.form.register_password.$model
        )
        .then(data => this.addUser(data))
        .catch(err => {
          this.message.error = err.message;
        });
    },

    addUser(data) {
      fire
        .database()
        .ref("/users")
        .child(`${data.user.uid}`)
        .update({ name: `${ this.$v.form.register_name.$model}`, remember: false })
        .then(() => {
          this.message.success = "Register Successfully";
          setTimeout(function() {
            window.location.href = "/message-app/#/login";
          }, 2000);
        });
    }
  }
};
</script>
<style>
@media screen and (max-width: 992px) {
  .bg-app {
    margin: 0 !important;
    height: 100vh !important;
  }
}
</style>
