import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Login from "@/views/Login";
import Register from "@/views/Register";
import Room from "@/views/Room";
import Profile from "@/views/Profile";
// import RoomDetail from "@/views/RoomDetail";

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    redirect: '/room'
  },
  {
    path: "/login",
    name: "Login",
    component: Login
  },
  {
    path: "/register",
    name: "register",
    component: Register
  },
  {
    path: "/room/:id",
    name: "Home",
    component: Home
  },
  {
    path: "/room",
    name: "Room",
    component: Room
  },
  {
    path: "/profile",
    name: "Profile",
    component: Profile
  },
  {
    path: "*",
    name: "Room",
    component: Room
  }

];

const router = new VueRouter({
  routes
});

export default router;
