<template>
  <div class="login">
    <div class="row justify-content-center">
      <div class="col-12 col-sm-12 col-md-12 col-lg-6 bg-app">
        <form @submit.prevent="signInWithEmailAndPassword()">
          <div class="form-auth">
            <h1>Login</h1>

            <div class="form-group mb-4">
              <div
                v-if="$v.form.$error"
                class="alert alert-danger mb-4"
                role="alert"
              >
                <p
                  v-if="!$v.form.login_id.email"
                  class="mb-0"
                  v-text=" 'Error - Email' "
                />
                <p
                  v-if="!$v.form.login_id.required"
                  class="mb-0"
                  v-text=" 'Error - Email' "
                />
                <p
                  v-if="!$v.form.login_password.required"
                  class="mb-0"
                  v-text=" 'Error - Password' "
                />
              </div>

              <div class="alert alert-danger" role="alert" v-if="error">
                {{ error }}
              </div>

            </div>

            <div class="form-group mb-4">
              <label for="login_id">Email address</label>
              <input
                type="email"
                class="form-control"
                id="login_id"
                name="login_id"
                v-model.trim="$v.form.login_id.$model"
                placeholder="Enter email"
              />
            </div>

            <div class="form-group mb-4">
              <label for="login_password">Password</label>
              <input
                type="password"
                class="form-control"
                id="login_password"
                name="login_password"
                v-model.trim="$v.form.login_password.$model"
                placeholder="Password"
              />
            </div>

            <div class="row">
              <div class="col-6">
                <a
                  v-bind:href="'/message-app/#/register'"
                  class="btn btn-outline-light w-100"
                  v-text="'Register'"
                />
              </div>
              <div class="col-6">
                <button
                  type="submit"
                  class="btn btn-custom"
                  v-text="'Login'"
                  :disabled="$v.form.$invalid || isSubmit"
                />
              </div>
            </div>
          </div>
        </form>

      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import fire from "../store/fire";
import { required, email } from "vuelidate/lib/validators";

Vue.directive("focus", {
  inserted(el) {
    el.focus();
  }
});

export default {
  name: "Home",

  components: {},

  data() {
    return {
      isSubmit: false,
      form: {
        login_id: "",
        login_password: ""
      },
      error: ""
    };
  },

  validations: {
    form: {
      login_id: { email, required },
      login_password: { required }
    }
  },

  methods: {
    async signInWithEmailAndPassword() {
      this.isSubmit = true;
      if (this.$v.form.$invalid) {
        this.isSubmit = false;
        return false;
      }

      await fire
        .auth()
        .signInWithEmailAndPassword(this.$v.form.login_id.$model,this.$v.form.login_password.$model)
        .then(() => {
          this.$router.push("room");
        })
        .catch(err => {
          this.error = err.message;
        });
    }
  }
};
</script>
<style>
.form-auth {
  height: 100%;
  margin-top: 15vh;
  padding: 0 60px;
}

.form-auth .form-group {
  text-align: left;
}

.form-auth .btn-custom {
  color: #fff;
  background-color: #563d7c;
  border: none;
  width: 100%;
}

.form-auth button:focus {
  border: 0 !important;
}

@media screen and (max-width: 992px) {
  .bg-app {
    margin: 0 !important;
    height: 100vh !important;
  }

  .form-auth {
    height: 100%;
    margin-top: 15vh;
    padding: 0 10px;
  }
}
</style>
